@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Raleway:wght@500&family=Roboto&family=Rubik&family=Russo+One&display=swap);
body{
  background: linear-gradient(to right,#03094d, #1b2d5c);
  color: #ddd;
  padding: 50px;
  font-family: raleway;
}
h1{
  color: #eef;
  text-align: center;
  font-size: 28px;
}
.group{
    background: hsl(142, 97%, 49%);
    width: 180px;
    margin: 0 auto 20px;
    display: grid;
    grid-template-columns: 100px 80px;
   border-radius: 25px; 
}
input,select{
    background: none;
    border:0;
    color:black;
    font-weight: bold;
    background: hsl(142, 97%, 49%);
    border-radius: 20px;
}
input{
    padding-left: 10px;
}
select{
    margin-right: 10px;
    padding: 10px;
    cursor: pointer;
}
